*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Noto Sans JP', sans-serif;
}
#previous{
    padding:0.5rem 1rem ;
    border : 1px solid black;
    border-radius: 0.3rem;
    display: inline-block;
    cursor: pointer;
    transition: 0.2s ease-in;
}
#previous:hover{
    color: yellow;
    background-color: black;
}

#add{
    padding:0.5rem 1rem ;
    border : 1px solid black;
    border-radius: 0.3rem;
    display: inline-block;
    cursor: pointer;
    transition: 0.2s ease-in;
}
#add:hover{
    color: yellow;
    background-color: black;
}

#pager{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}
#film-list{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
}
.film {
    width: 45vw;
}
